<template>
  <div class="promocode">
    <Button
      class="promocode__button"
      :class="{ active: isOpen }"
      @click="toggleAccordion"
      type="text"
    >
      Ввести промокод
      <svg
        width="20"
        height="20"
        viewBox="0 0 20 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M5 12.5L10 7.5L15 12.5"
          stroke="#2196F3"
          stroke-width="1.5"
          stroke-linecap="round"
          stroke-linejoin="round"
        />
      </svg>
    </Button>

    <div v-if="isOpen" class="promocode__dropdown">
      <Input
        type="text"
        v-model="promoCode"
        placeholder="Введите промокод"
        label="Промокод"
      />
      <Button @click="applyPromoCode" size="medium">Применить</Button>
    </div>
  </div>
</template>

<script>
import Button from "../button/Button.vue";
import Input from "./Input.vue";

export default {
  data() {
    return {
      isOpen: false,
      promoCode: "",
    };
  },
  components: { Button, Input },
  methods: {
    toggleAccordion(event) {
      event.preventDefault();
      this.isOpen = !this.isOpen;
    },
    applyPromoCode() {
      // Логика применения промокода
      this.promoCode = ""; // Очистка поля после применения
    },
  },
};
</script>

