@import "/src/styles/common.scss";

.promocode {
  &__button {
    & svg {
      margin-left: 4px;
    }

    @include hoverAndActive {
      & > svg path {
        stroke: $main900;
      }
    }

    &.active {
      & svg {
        transform: rotate(180deg);
      }
