<template>
  <div
    class="search__input search__input_last"
    @click="toggleDropdown"
    ref="selectContainer"
  >
    <svg
      width="20"
      height="20"
      viewBox="0 0 20 20"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M2.5 17.5V15.8333C2.5 14.9493 2.85119 14.1014 3.47631 13.4763C4.10143 12.8512 4.94928 12.5 5.83333 12.5H9.16667C10.0507 12.5 10.8986 12.8512 11.5237 13.4763C12.1488 14.1014 12.5 14.9493 12.5 15.8333V17.5M13.3333 2.60833C14.0503 2.79192 14.6859 3.20892 15.1397 3.79359C15.5935 4.37827 15.8399 5.09736 15.8399 5.8375C15.8399 6.57764 15.5935 7.29673 15.1397 7.88141C14.6859 8.46608 14.0503 8.88308 13.3333 9.06667M17.5 17.5V15.8333C17.4958 15.0976 17.2483 14.384 16.7961 13.8037C16.3439 13.2233 15.7124 12.8089 15 12.625M4.16667 5.83333C4.16667 6.71739 4.51786 7.56523 5.14298 8.19036C5.7681 8.81548 6.61595 9.16667 7.5 9.16667C8.38405 9.16667 9.2319 8.81548 9.85702 8.19036C10.4821 7.56523 10.8333 6.71739 10.8333 5.83333C10.8333 4.94928 10.4821 4.10143 9.85702 3.47631C9.2319 2.85119 8.38405 2.5 7.5 2.5C6.61595 2.5 5.7681 2.85119 5.14298 3.47631C4.51786 4.10143 4.16667 4.94928 4.16667 5.83333Z"
        stroke="#9E9E9E"
        stroke-width="1.5"
        stroke-linecap="round"
        stroke-linejoin="round"
      />
    </svg>
    <div class="search__input-content">
      <div class="search__input-value">{{ getGuestForm }}</div>
    </div>

    <svg
      class="select__arrow"
      :class="{ select__arrow_open: isDropdownOpen }"
      width="20"
      height="20"
      viewBox="0 0 20 20"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M5 7.5L10 12.5L15 7.5"
        stroke="#424242"
        stroke-width="1.5"
        stroke-linecap="round"
        stroke-linejoin="round"
      />
    </svg>

    <div class="select__dropdown" v-if="isDropdownOpen" @click.stop>
      <div class="select__option">
        Взрослые
        <div class="select__increments">
          <span
            class="select__increment"
            :class="{ select_disabled: adultCount <= 1 }"
            @click="selectAdultMunus"
          >
            <svg
              width="30"
              height="30"
              viewBox="0 0 30 30"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect
                x="0.5"
                y="0.5"
                width="29"
                height="29"
                rx="14.5"
                stroke="#EEEEEE"
              />
              <path
                d="M10.3335 15H19.6668"
                stroke="#424242"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg> </span
          ><span>{{ adultCount }}</span
          ><span class="select__increment" @click="selectAdultPlus"
            ><svg
              width="30"
              height="30"
              viewBox="0 0 30 30"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect
                x="0.5"
                y="0.5"
                width="29"
                height="29"
                rx="14.5"
                stroke="#EEEEEE"
              />
              <path
                d="M15.0002 10.333V19.6663M10.3335 14.9997H19.6668"
                stroke="#424242"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </span>
        </div>
      </div>
      <div
        v-for="(childAge, index) in children"
        :key="index"
        class="select__option-child select__option-child_disable"
      >
        {{ childAge }}
        <svg
          class="select__remove"
          @click="removeChild(index)"
          width="20"
          height="20"
          viewBox="0 0 20 20"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M15 5L5 15M5 5L15 15"
            stroke="#424242"
            stroke-width="1.5"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
      </div>
      <select
        class="select__option-child select__option-child_select"
        ref="hiddenSelect"
        v-model="selectedChildAge"
        @change="addChildAge"
      >
        <option value="">Добавить ребенка</option>
        <option
          class="select__child"
          v-for="age in ages"
          :key="age"
          :value="age"
        >
          {{ age }}
        </option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isDropdownOpen: false,
      showChildSelect: false,
      adultCount: 1,
      children: [],
      selectedChildAge: "",
      ages: [
        "До 1 года",
        "1 год",
        "2 года",
        "3 года",
        "4 года",
        "5 лет",
        "6 лет",
        "7 лет",
        "8 лет",
        "9 лет",
        "10 лет",
        "11 лет",
        "12 лет",
        "13 лет",
        "14 лет",
        "15 лет",
        "16 лет",
        "17 лет",
        "18 лет",
      ],
    };
  },
  computed: {
    getGuestForm() {
      const count = this.adultCount + this.children.length;
      if (count % 10 === 1 && count % 100 !== 11) {
        return `${count} гость`;
      } else if (
        count % 10 >= 2 &&
        count % 10 <= 4 &&
        (count % 100 < 10 || count % 100 >= 20)
      ) {
        return `${count} гостя`;
      } else {
        return `${count} гостей`;
      }
    },
  },
  methods: {
    toggleDropdown() {
      this.isDropdownOpen = !this.isDropdownOpen;
    },
    selectAdultMunus() {
      this.adultCount -= 1;
    },
    selectAdultPlus() {
      this.adultCount += 1;
    },
    removeChild(index) {
      this.children.splice(index, 1);
    },
    addChildAge() {
      if (this.selectedChildAge) {
        this.children.push(this.selectedChildAge);
        this.selectedChildAge = "";
      }
    },
    handleClickOutside(event) {
      const selectContainer = this.$refs.selectContainer;

      if (selectContainer && !selectContainer.contains(event.target)) {
        this.isDropdownOpen = false;
      }
    },
  },
  mounted() {
    document.addEventListener("click", this.handleClickOutside);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.handleClickOutside);
  },
};
</script>

<style lang="scss">
@import "/src/styles/common.scss";
