<template>
  <div
    class="form-input"
    :class="{
      'full-width': fullWidth,
    }"
  >
    <div class="form-input__label">{{ label }}</div>
    <div
      class="form-input__input"
      :class="{
        'is-error': isError,
      }"
      @click="toggleDropdown"
      ref="selectContainer"
    >
      <div class="form-input__icon" v-html="icon" v-if="!!icon"></div>
      <component
        :is="isTextarea ? 'textarea' : 'input'"
        :rows="isTextarea ? 3 : undefined"
        v-if="!mask"
        :type="type || 'text'"
        class="form-input__text"
        :class="{
          'no-pointer': noPointer,
          'no-icon': !icon,
        }"
        :placeholder="placeholder"
        :value="selectedValue"
        @input="onInput"
        @focus="toggleDropdown"
      />
      <MaskInput
        v-else
        class="form-input__text"
        :placeholder="placeholder"
        :mask="mask"
        @input="onInput"
      />
      <svg
        class="form-input__arrow"
        :class="{ open: isDropdownOpen }"
        width="20"
        height="20"
        viewBox="0 0 20 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        v-if="noPointer"
      >
        <path
          d="M5 7.5L10 12.5L15 7.5"
          stroke="#424242"
          stroke-width="1.5"
          stroke-linecap="round"
          stroke-linejoin="round"
        />
      </svg>
    </div>

    <div class="form-input__dropdown" v-if="isDropdownOpen" @click.stop>
      <div
        v-for="option in options"
        :key="option.value"
        class="form-input__dropdown-option"
        @click="selectOption(option)"
      >
        <span>{{ option.value }}</span>
        {{ option.label }}
      </div>
    </div>
  </div>
</template>

<script>
import { MaskInput } from "vue-3-mask";

export default {
  props: {
    type: {
      type: String,
    },
    value: {
      type: String,
    },
    label: {
      type: String,
    },
    icon: {
      type: String,
    },
    options: {
      type: Array,
    },
    placeholder: {
      type: String,
    },
    noPointer: {
      type: Boolean,
    },
    mask: {
      type: String,
    },
    fullWidth: {
      type: Boolean,
    },
    isTextarea: {
      type: Boolean,
      default: false,
    },
    isError: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isDropdownOpen: false,
      selectedValue: this.value,
    };
  },
  components: {
    MaskInput,
  },
  methods: {
    toggleDropdown() {
      if (!this.options?.length) {
        return;
      }
      this.isDropdownOpen = !this.isDropdownOpen;
    },
    selectOption(option) {
      this.selectedValue = option.value;
      this.isDropdownOpen = false;
      this.$emit("input", option.value); // Emit the selected value
    },
    onInput(event) {
      this.selectedValue = event.target.value;
      this.$emit("input", this.selectedValue); // Emit the input value
    },
    handleClickOutside(event) {
      const selectContainer = this.$refs.selectContainer;

      if (selectContainer && !selectContainer.contains(event.target)) {
        this.isDropdownOpen = false;
      }
    },
  },
  mounted() {
    document.addEventListener("click", this.handleClickOutside);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.handleClickOutside);
  },
};
</script>

<style lang="scss">
@import "/src/styles/common.scss";
@import "./form-input.scss";
</style>
