<template>
  <div class="hotel-info__list" ref="list">
    <div class="hotel-info__item" v-for="(item, index) in items" :key="index">
      {{ item }}<span></span>
    </div>
    <Button class="hotel-info__button" type="text" @click="handleClick"
      >Подробнее
      <svg
        width="20"
        height="20"
        viewBox="0 0 20 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M7.5 5L12.5 10L7.5 15"
          stroke="#2196F3"
          stroke-width="1.5"
          stroke-linecap="round"
          stroke-linejoin="round"
        />
      </svg>
    </Button>
  </div>
</template>

<script>
import Button from "../button/Button.vue";

export default {
  components: {
    Button,
  },
  data() {
    return {
      items: [
        "Телевизор",
        "Холодильник",
        "Парковка",
        "Ванные принадлежности",
        "Балкон",
        "Кондиционер",
        "Кухонная плита",
      ],
    };
  },
  methods: {
    // можно еще на ресайз ее добавить.
    // надо добавить еще на прееключение роутов (если надо будет) не работает там
    trimItems() {
      const listHeight = this.$refs.list ? this.$refs.list.clientHeight : 0;

      if (listHeight > 48 && this.items.length) {
        this.items = this.items.slice(0, -1);

        this.$nextTick(() => {
          this.trimItems(); // Рекурсивный вызов после обновления DOM
        });
      }
    },
    handleClick(event) {
      event.preventDefault();

      this.$emit("details-clicked");
    },
  },
  mounted() {
    this.trimItems();
  },
};
</script>
