<template>
  <div
    class="mobile-select"
    :class="{ 'child-select-open': isDropdownOpen }"
    ref="selectContainer"
  >
    <div class="mobile-select__content" @click="toggleDropdown">
      <div class="mobile-select__text">{{ getGuestForm }}</div>
      <Button class="mobile-select__button" type="text"
        >Изменить
        <svg
          width="20"
          height="20"
          viewBox="0 0 20 20"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M7.5 5L12.5 10L7.5 15"
            stroke="#2196F3"
            stroke-width="1.5"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
      </Button>
    </div>

    <div class="mobile-select__dropdown" v-if="isDropdownOpen" @click.stop>
      <div class="mobile-select__option">
        Взрослые
        <div class="mobile-select__increments">
          <span
            class="mobile-select__increment"
            :class="{ select_disabled: adultCount <= 1 }"
            @click="selectAdultMunus"
          >
            <svg
              width="30"
              height="30"
              viewBox="0 0 30 30"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect
                x="0.5"
                y="0.5"
                width="29"
                height="29"
                rx="14.5"
                stroke="#EEEEEE"
              />
              <path
                d="M10.3335 15H19.6668"
                stroke="#424242"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg> </span
          ><span>{{ adultCount }}</span
          ><span class="mobile-select__increment" @click="selectAdultPlus"
            ><svg
              width="30"
              height="30"
              viewBox="0 0 30 30"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect
                x="0.5"
                y="0.5"
                width="29"
                height="29"
                rx="14.5"
                stroke="#EEEEEE"
              />
              <path
                d="M15.0002 10.333V19.6663M10.3335 14.9997H19.6668"
                stroke="#424242"
                stroke-width="1.5"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </span>
        </div>
      </div>
      <div
        v-for="(childAge, index) in children"
        :key="index"
        class="mobile-select__option-child select__option-child_disable"
      >
        {{ childAge }}
        <svg
          class="mobile-select__remove"
          @click="removeChild(index)"
          width="20"
          height="20"
          viewBox="0 0 20 20"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M15 5L5 15M5 5L15 15"
            stroke="#424242"
            stroke-width="1.5"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
      </div>
      <select
        class="mobile-select__option-child select__option-child_select"
        ref="hiddenSelect"
        v-model="selectedChildAge"
        @change="addChildAge"
      >
        <option value="">Добавить ребенка</option>
        <option
          class="mobile-select__child"
          v-for="age in ages"
          :key="age"
          :value="age"
        >
          {{ age }}
        </option>
      </select>
    </div>

    <div
      class="mobile-select__content mobile-select__calendar"
      @click="toggleDropdown"
    >
      <div class="mobile-select__text">Заезд</div>
      <Button class="mobile-select__button" type="text"
        >Выбрать дату
        <svg
          width="20"
          height="20"
          viewBox="0 0 20 20"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M7.5 5L12.5 10L7.5 15"
            stroke="#2196F3"
            stroke-width="1.5"
            stroke-linecap="round"
            stroke-linejoin="round"
          />
        </svg>
      </Button>
    </div>
  </div>
</template>

<script>
import Button from "/src/components/button/Button";

export default {
  components: { Button },
  data() {
    return {
      isDropdownOpen: false,
      showChildSelect: false,
      adultCount: 1,
      children: [],
      selectedChildAge: "",
      ages: [
        "До 1 года",
        "1 год",
        "2 года",
        "3 года",
        "4 года",
        "5 лет",
        "6 лет",
        "7 лет",
        "8 лет",
        "9 лет",
        "10 лет",
        "11 лет",
        "12 лет",
        "13 лет",
        "14 лет",
        "15 лет",
        "16 лет",
        "17 лет",
        "18 лет",
      ],
      oldValue: "",
    };
  },
  computed: {
    getGuestForm() {
      const count = this.adultCount + this.children.length;
      if (count % 10 === 1 && count % 100 !== 11) {
        return `${count} гость`;
      } else if (
        count % 10 >= 2 &&
        count % 10 <= 4 &&
        (count % 100 < 10 || count % 100 >= 20)
      ) {
        return `${count} гостя`;
      } else {
        return `${count} гостей`;
      }
    },
  },
  methods: {
    toggleDropdown(event) {
      event.preventDefault();
      this.isDropdownOpen = !this.isDropdownOpen;

      // Для родительского календаря
      const header = document.querySelector(".vanilla-calendar-info__title");
      const calendar = document.querySelector(".vanilla-calendar");

      if (header && calendar) {
        if (this.isDropdownOpen) {
          this.oldValue = header.textContent;

          header.textContent = "Список гостей";
          calendar.classList.add("child-select-open");
        } else {
          header.textContent = this.oldValue;
          calendar.classList.remove("child-select-open");
        }
      }
    },
    selectAdultMunus() {
      this.adultCount -= 1;
    },
    selectAdultPlus() {
      this.adultCount += 1;
    },
    removeChild(index) {
      this.children.splice(index, 1);
    },
    addChildAge() {
      if (this.selectedChildAge) {
        this.children.push(this.selectedChildAge);
        this.selectedChildAge = "";
      }
    },
  },
};
</script>

<style lang="scss">
@import "/src/styles/common.scss";
@import "./mobile-select.scss";
</style>
