<template>
  <div class="gallery">
    <div v-if="isMobile" class="swiper-container">
      <swiper
        :modules="modules"
        :slides-per-view="1"
        :pagination="{ clickable: true }"
      >
        <swiper-slide v-for="(photo, index) in photos" :key="index">
          <img :src="photo" alt="Фотография" @click="openLightbox" />
        </swiper-slide>
      </swiper>
    </div>
    <div class="large-photo" @click="openLightbox" v-else>
      <img :src="selectedPhoto" alt="Большая фотография" />
    </div>
    <div class="small-photos">
      <div
        v-for="(photo, index) in photos.slice(0, 3)"
        :key="index"
        class="small-photo"
        @click="selectPhoto(photo)"
      >
        <img class="small-photo" :src="photo" alt="Маленькая фотография" />
      </div>
      <div class="more-photos" @click="openLightbox" v-if="photos.length > 3">
        <img :src="photos[4]" alt="Маленькая фотография" />
        <div class="more-photos__text">+{{ photos.length - 3 }} фото</div>
      </div>
    </div>
  </div>
  <vue-easy-lightbox
    :visible="isLightboxVisible"
    :imgs="photos"
    :index="currentIndex"
    @hide="isLightboxVisible = false"
  />
</template>

<script>
import { Pagination } from "swiper/modules";
import VueEasyLightbox from "vue-easy-lightbox";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/swiper-bundle.css";

export default {
  components: {
    VueEasyLightbox,
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      photos: [
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
        require("/src/assets/img/4.png"),
      ],
      selectedPhoto: "",
      isLightboxVisible: false,
      currentIndex: 0,
      isMobile: false,
      modules: [Pagination],
    };
  },
  methods: {
    selectPhoto(photo) {
      this.selectedPhoto = photo;
      this.currentIndex = this.photos.indexOf(photo);
    },
    openLightbox() {
      this.isLightboxVisible = true;
    },
    handleMediaChange(event) {
      this.isMobile = event.matches;
    },
  },
  mounted() {
    this.selectedPhoto = this.photos[0]; // Устанавливаем первую фотографию как выбранную

    const mediaQuery = window.matchMedia("(max-width: 1023px)");

    this.handleMediaChange(mediaQuery);

    // Проверяем, поддерживается ли addEventListener
    if (mediaQuery.addEventListener) {
      mediaQuery.addEventListener("change", this.handleMediaChange);
    } else if (mediaQuery.addListener) {
      mediaQuery.addListener(this.handleMediaChange); // Используем addListener для iOS
    }
  },
  beforeUnmount() {
    const mediaQuery = window.matchMedia("(max-width: 1023px)");

    // Проверяем, поддерживается ли addEventListener
    if (mediaQuery.addEventListener) {
      mediaQuery.removeEventListener("change", this.handleMediaChange);
    } else if (mediaQuery.addListener) {
      mediaQuery.removeListener(this.handleMediaChange); // Используем addListener для iOS
    }
  },
};
</script>

<style lang="scss">
@import "/src/styles/common.scss";
@import "./gallery.scss";
</style>
