<template>
  <div class="main-page">
    <Header />
    <div v-if="isOpenPopup">
      <Overlay :class="{ active: isOpenPopup }" @click="handleClick" />
      <BasketPage @back-clicked="handleClick" />
    </div>
    <Button @click="handleClick" style="width: max-content; margin: 20px auto"
      >Открыть попап</Button
    >
  </div>
</template>

<script>
import BasketPage from "../BasketPage/BasketPage.vue";
import Button from "../button/Button.vue";
import Header from "../header/Header.vue";
import Overlay from "../overlay/Overlay.vue";

export default {
  components: { Header, Overlay, BasketPage, Button },
  data() {
    return {
      isOpenPopup: true,
    };
  },
  methods: {
    handleClick(event) {
      event.preventDefault();
      this.isOpenPopup = !this.isOpenPopup;
    },
  },
};
</script>
