<template>
  <a
    :class="buttonClasses"
    @click="handleClick"
    :href="href"
    :target="target"
    rel="noopener noreferrer"
  >
    <span v-if="buttonText">{{ buttonText }}</span>
    <slot v-else />
  </a>
</template>

<script>
export default {
  name: "MainButton",
  props: {
    buttonText: {
      type: String,
    },
    isCircle: {
      type: Boolean,
    },
    type: {
      type: String,
      default: "primary",
    },
    size: {
      type: String,
      default: "medium",
    },
    target: {
      type: String,
      default: "_blank",
    },
    href: {
      type: String,
      default: "",
    },
  },
  computed: {
    buttonClasses() {
      return {
        "button-primary": this.type === "primary",
        "button-secondary": this.type === "secondary",
        "button-text": this.type === "text",
        "is-circle": this.isCircle,
        [this.size]: true,
      };
    },
  },
};
</script>

<style lang="scss">
@import "./button.scss";
</style>
