import { createRouter, createWebHashHistory } from "vue-router";
import { createApp } from "vue";
import App from "./App.vue";
import Main from "./components/main/Main.vue";
import MainLoading from "./components/main/MainLoading.vue";
import MainError from "./components/main/MainError.vue";
import MainBasket from "./components/main/MainBasket.vue";
import MainOther from "./components/main/MainOther.vue";
import MainSuccess from "./components/main/MainSuccess.vue";
import MainSuccessMobile from "./components/main/MainSuccessMobile.vue";

const routes = [
  { path: "/", component: Main },
  { path: "/loading", component: MainLoading },
  { path: "/error", component: MainError },
  { path: "/basket", component: MainBasket },
  { path: "/other", component: MainOther },
  { path: "/success", component: MainSuccess },
  { path: "/success-mobile", component: MainSuccessMobile },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

const app = createApp(App);

app.use(router);

app.mount("#app");
