          stroke-linejoin="round"
        />
      </svg>
      <svg
        class="close-window__icon"
        width="136"
        height="136"
        viewBox="0 0 136 136"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <rect width="136" height="136" rx="68" fill="#FFF6DC" />
        <path
          d="M68 83V83.03M68 72.5C67.9448 71.5261 68.2075 70.5607 68.7486 69.749C69.2896 68.9374 70.0798 68.3236 71 68C72.1276 67.5688 73.1398 66.8817 73.9567 65.9929C74.7737 65.104 75.3732 64.0377 75.708 62.8778C76.0428 61.7179 76.1038 60.4961 75.8862 59.3086C75.6686 58.1211 75.1783 57.0004 74.4539 56.0345C73.7296 55.0687 72.7909 54.2843 71.7119 53.7429C70.6328 53.2015 69.4428 52.9179 68.2356 52.9146C67.0283 52.9112 65.8368 53.1882 64.7547 53.7236C63.6727 54.259 62.7297 55.0382 62 56M41 68C41 71.5457 41.6984 75.0567 43.0553 78.3325C44.4121 81.6082 46.4009 84.5847 48.9081 87.0919C51.4153 89.5991 54.3918 91.5879 57.6675 92.9447C60.9433 94.3016 64.4543 95 68 95C71.5457 95 75.0567 94.3016 78.3325 92.9447C81.6082 91.5879 84.5847 89.5991 87.0919 87.0919C89.5991 84.5847 91.5879 81.6082 92.9447 78.3325C94.3016 75.0567 95 71.5457 95 68C95 64.4543 94.3016 60.9433 92.9447 57.6675C91.5879 54.3918 89.5991 51.4153 87.0919 48.9081C84.5847 46.4009 81.6082 44.4121 78.3325 43.0553C75.0567 41.6984 71.5457 41 68 41C64.4543 41 60.9433 41.6984 57.6675 43.0553C54.3918 44.4121 51.4153 46.4009 48.9081 48.9081C46.4009 51.4153 44.4121 54.3918 43.0553 57.6675C41.6984 60.9433 41 64.4543 41 68Z"
          stroke="#FFC107"
          stroke-width="4"
