          stroke-linejoin="round"
        />
      </svg>

      <h3 class="close-window__title">Вы точно хотите закрыть окно?</h3>
      <p class="close-window__text">
        При закрытии будут потеряны ваши данные по бронированию.
      </p>
      <div class="close-window__buttons">
        <Button type="secondary" @click="cancel">Отмена</Button>
        <Button @click="close">Закрыть</Button>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "../button/Button.vue";
import Overlay from "../overlay/Overlay.vue";

export default {
  data() {
    return {
      isMobile: false,
    };
  },
  components: {
    Button,
    Overlay,
  },
  methods: {
    cancel() {
      // Логика для отмены закрытия окна
      this.$emit("cancel");
    },
    close() {
      // Логика для закрытия окна
      this.$emit("close");
    },
    handleMediaChange(event) {
      this.isMobile = event.matches;
    },
  },
  mounted() {
    const mediaQuery = window.matchMedia("(max-width: 1023px)");

    this.handleMediaChange(mediaQuery);

    // Проверяем, поддерживается ли addEventListener
    if (mediaQuery.addEventListener) {
      mediaQuery.addEventListener("change", this.handleMediaChange);
    } else if (mediaQuery.addListener) {
      mediaQuery.addListener(this.handleMediaChange); // Используем addListener для iOS
    }
  },
  beforeUnmount() {
    const mediaQuery = window.matchMedia("(max-width: 1023px)");

    // Проверяем, поддерживается ли addEventListener
    if (mediaQuery.addEventListener) {
      mediaQuery.removeEventListener("change", this.handleMediaChange);
    } else if (mediaQuery.addListener) {
      mediaQuery.removeListener(this.handleMediaChange); // Используем addListener для iOS
    }
  },
};
</script>

